.total {
  display: block;
  justify-content: space-between;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 0.5rem 0;
  
}
.ul {
  list-style: none;
}
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 7px;
  font-weight: 700;
  color: rgb(121, 117, 117);
  font-size: 30px;
  cursor: pointer;
}
.total h2 {
  font-family: 'Metal Mania';
  letter-spacing: 5px;
  font-size: 30px;
}

.wrapper {
  display: flex;
  justify-content: space-around;
  /* margin: 0 10px; */
  max-width: 900px;
  margin: 0 auto;
  padding: 20px 30px;
}

.item {
  display: flex;
  margin-left: 18px;
  width: 30%;
  font-size: 1.2rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  align-items: center;
  border-bottom: 1px solid black;
}

.price {
  display: flex;
  width: 20%;
  font-size: 1.2rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  align-items: center;
  border-bottom: 1px solid black;

}

.quantity {
  display: flex;
  width: 20%;
  font-size: 1.2rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  align-items: center;
  border-bottom: 1px solid black;
}

.cart-total {
    display: flex;
    flex-direction: row-reverse;
    right: 0;
    margin: 20px;
}

.cart-total span {
   padding: 0 10px;
}
.container {
  display: flex;
  justify-content: space-between;
  padding: 0 50px 0 0;
  text-align: center;
}

.container span {
  padding: 0 1px;
  margin: 5px;
  font-size: 18px;
  text-align: center;
}

.cart-column {
  display: flex;
  width: 35%;
  align-items: center;
  border-bottom: 1px solid black;
}
.container img {
  width: 50px;
  border-radius: 8%;
  margin: 10px;
}

.quantity {
  display: flex;
  width: 25%;
  align-items: center;
  border-bottom: 1px solid black;
}

.price {
  display: flex;
  width: 20%;
  align-items: center;
  border-bottom: 1px solid black;
}

.container button {
  background-color: rgb(231, 76, 76);
  padding: 8px 7px;
  color: white;
  border: none;
  cursor: pointer;
  margin: 0 12px;
  border-radius: 8%;
  font-weight: bold;
}
